.skillsBlock {
    display: flex;
    min-height: 100vh;
    background-color: #8e7da8;
    align-items:center;

}

.skillsContainer {
    border: 1px solid black;

    justify-content: center;
    align-content: space-between;

}

.skills {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
}

@media screen and (max-width: 769px) {
    .skills  {
        justify-content: center;
    }
}
